const NAME = 'wc_product_dimensions';
const KEY = 'dimensions';

AC_SERVICES.addListener( 'Editing.Editables.Ready', ( Editables ) => {
	const Abstract = Editables.get( '_abstract' );
	const i18N = {
		WIDTH : acp_woocommerce_i18n.woocommerce.width,
		LENGTH : acp_woocommerce_i18n.woocommerce.length,
		HEIGHT : acp_woocommerce_i18n.woocommerce.height,
	};

	class Dimensions extends Abstract {

		focus(){
			this.getElement().querySelector( '[name=length]' ).focus();
		}

		getEditableType() {
			return NAME;
		}

		valueToInput( value ) {
			this.getElement().querySelector( '[name=length]' ).focus();

			if ( !value ) {
				return;
			}

			this.getElement().querySelector( '[name=length]' ).value = value.length;
			this.getElement().querySelector( '[name=width]' ).value = value.width;
			this.getElement().querySelector( '[name=height]' ).value = value.height;
		}

		getValue() {
			return {
				length : this.getElement().querySelector( '[name=length]' ).value,
				width : this.getElement().querySelector( '[name=width]' ).value,
				height : this.getElement().querySelector( '[name=height]' ).value
			};
		}

		getTemplate() {
			const length = this.template.form.input( 'length', null, { type : 'number', step : 'any', placeholder : i18N.LENGTH } ).outerHTML;
			const width = this.template.form.input( 'width', null, { type : 'number', step : 'any', placeholder : i18N.WIDTH } ).outerHTML;
			const height = this.template.form.input( 'height', null, { type : 'number', step : 'any', placeholder : i18N.HEIGHT } ).outerHTML;

			return `<div class="aceditable__form__inputs__dimensions">${length} ${width} ${height}</div>`;
		}

	}

	Editables.registerEditable( 'dimensions', Dimensions );

} );

AC_SERVICES.addListener( 'Editing.Middleware.Ready', ( Middleware ) => {
	const Abstract = Middleware.getClass( '_abstract' );

	class Dimensions extends Abstract {
		getEditable() {
			return this.Editables.get( KEY );
		}
	}

	Middleware.register( KEY, Dimensions );
} );